.divApp{
    margin-top: 130px;
    height: fit-content;
    min-height: 80vh;
}
#divAppHome > h2{
    text-align: center;
}
#divAppHome > p{
    padding: 0 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}
#divAppHome > p > span{
    width: 100%;
    max-width: 600px;
}
#contenedorIMGfondo{
    height: 250px;
    background: url('https://paginasamarillascolombianas.com/wp-content/themes/mace/img/backsolo.jpg');
    background-size: 100%;
    background-position: cover;
    display: grid;
    place-items: center;
}
#contenedorIMGfondo > h1{
    max-width: 500px;
    min-width: 350px;
    color: var(--fontColorPrincipal2);
    text-shadow: 2px 2px #aaa;
    text-align: justify;
}

/* nosotros */
#divAppNosotros{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
#divAppNosotros > div:nth-child(1){
    width: 100%;
    max-width: 500px;
}
#divAppNosotros > div:nth-child(1) > h1{
    text-align: center;
}
#divAppNosotros > div:nth-child(1) > p{
    text-align: justify;
    padding: 0 30px;
    box-sizing: border-box;
}
#divAppNosotros > div:nth-child(2){
    width: 380px;
    height: 246px;
    box-shadow: 5px 0 10px 10px rgba(235, 235, 235, 0.395);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 12px;
}
#divAppNosotros > div:nth-child(2):hover{
    background: rgb(221,221,255);
    background: linear-gradient(159deg, rgba(221,221,255,0.27913163556438203) 3%, rgba(238,238,238,0.3323529240797881) 44%, rgba(255,255,255,1) 79%, rgba(255,255,255,1) 80%);
}

/* contacto */
#divAppContacto > h1{
    text-align: center;
}
#divAppContacto > h2{
    padding: 0 30px;
    box-sizing: border-box;
    text-align: center;
    font-weight: lighter;
}
#divAppContacto > form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 600px;
    padding: 0 30px;
    box-sizing: border-box;
}
.campContactForm{
    width: 100%;
    max-width: 400px;
    height: 35px;
    border: none;
    border-radius: 8px;
    outline: none;
    border-bottom: 1px solid var(--fondoSeguro);
}
.campContactForm:hover{
    border-bottom: none;
}
.campContactForm:focus{
    border-bottom: 1px solid var(--colorPrincipal);
    background: rgb(238,238,238);
    background: linear-gradient(90deg, rgba(238,238,238,0.18109241987810754) 25%, rgba(255,255,255,1) 60%); 
}
#divAppContacto > form > textarea{
    resize: none;
    height: 200px;
}

/* registrarse */
#divAppRegistrarse > h1{
    text-align: center;
    padding: 0 30px;
    box-sizing: border-box;
    font-size: var(--textoGrande);
}
#divAppRegistrarse form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 1160px;
    padding: 0 30px;
    box-sizing: border-box;
}
.radioForm{
    margin-right: 45px;
}

/* responsive */
@media (max-width: 500px){
    #contenedorIMGfondo > h1{
        margin: 0 25px;
    }
}